import React, { useEffect } from 'react'
import { ProfileControls } from '../ProfileControls'
import * as Yup from 'yup'
import { useDispatch } from 'react-redux'
import { Formik } from 'formik'
import { Form } from 'formik-antd'
import { MailOutlined } from '@ant-design/icons'
import { Input, ErrorsComponent } from 'src/components'
import { emailUpdate } from 'rdx/ducks/auth'
import { EmailUpdateFormData } from 'types/user'
import { useSelector } from 'rdx/index'
import { formErrorsActions, formErrorSelector } from 'rdx/ducks/formErrors'
import { Forms } from 'types/form'
import { userSelector } from 'rdx/ducks/auth'
import { ErrorStyled } from './styles'

export const EmailSettings: React.FC<{}> = (props) => {
  const dispatch = useDispatch()
  const networkError = useSelector((state) =>
    formErrorSelector(state, Forms.emailSettings),
  )
  const userData = useSelector((state) => userSelector(state))

  useEffect(() => {
    dispatch(formErrorsActions.resetFormError(Forms.emailSettings))
  }, [])

  const EmailSettingsSchema = Yup.object().shape({
    email: Yup.string()
      .required('Email is Required')
      .email('Invalid email address')
      .notOneOf([Yup.ref('old_email'), null], 'Email must not match'),
  })

  const initialValues: EmailUpdateFormData = {
    old_email: userData!.email,
    email: '',
  }

  return (
    <div>
      <Formik
        initialValues={initialValues}
        validationSchema={EmailSettingsSchema}
        onSubmit={({ email }) => {
          dispatch(emailUpdate(email))
        }}
        validateOnChange={false}
        validateOnBlur={false}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          submitForm,
        }) => (
          <Form>
            <ProfileControls
              title="Личные данные"
              subtitle="Изменить почту учетной записи"
              onSubmit={submitForm}
            />
            <div>
              <div>
                <div className="mb-15">
                  <Input
                    type="email"
                    name="old_email"
                    placeholder="Текущая почта"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.old_email}
                    prefix={<MailOutlined />}
                    disabled={true}
                  />
                </div>
                <div className="mb-15">
                  <Input
                    type="email"
                    name="email"
                    placeholder="Новая почта"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.email}
                    prefix={<MailOutlined />}
                  />
                </div>
              </div>
            </div>

            <ErrorsComponent
              errors={errors}
              touched={touched}
              networkError={networkError}
              StyledComponent={ErrorStyled}
            />
          </Form>
        )}
      </Formik>
    </div>
  )
}
