import type { AppState } from 'rdx/index'

export const authorizedSelector = (state: AppState) => state.auth.isAuthorized

export const authIsFetchingSelector = (state: AppState) => state.auth.isFetching

export const authIsCheckSessionFetchingSelector = (state: AppState) => state.auth.isCheckSessionFetching

export const userSelector = (state: AppState) => state.auth.user

