import React from 'react'
import { Button } from 'src/components'
import { Title, Text } from 'src/components'
import { Link } from 'react-router-dom'
import {
  ProfileControlsStyled,
  ControlsStyled,
  SaveButtonStyled,
} from './styles'

type ProfileControlsProps = {
  title: string
  subtitle: string
  onSubmit?: () => void
  submitButtonText?: string
}

export const ProfileControls: React.FC<ProfileControlsProps> = (props) => {
  const { title, subtitle, onSubmit, submitButtonText } = props

  return (
    <ProfileControlsStyled>
      <div>
        <Title text={title} size="smallest" />
        <Text text={subtitle} size="small" />
      </div>

      <ControlsStyled>
        {onSubmit && (
          <SaveButtonStyled>
            <Button customtype="primary" onClick={onSubmit}>
              {submitButtonText || 'Сохранить'}
            </Button>
          </SaveButtonStyled>
        )}
        <Button customtype="default">
          <Link to="/">Отменить</Link>
        </Button>
      </ControlsStyled>
    </ProfileControlsStyled>
  )
}
