import React from 'react'
import { Button, Title, AuthorizationOnly } from 'src/components'
import { Link } from 'react-router-dom'
import { EventStyled, ImageStyled, EventContentStyled, TitleStyled, ImagePlaceholderStyled } from './styles'

type EventProps = {
  id: string
}

const AuthButton = () => {
  return (
    <Link to="/auth">
      <Button customtype="default">Авторизоваться</Button>
    </Link>
  )
}

const ImagePlaceholder = () => {
  return <ImagePlaceholderStyled />
}

export const Event: React.FC<EventProps> = (props) => {
  const { id } = props;

  return (
    <EventStyled>
      <AuthorizationOnly fallback={<ImagePlaceholder />}>
        <ImageStyled src="https://via.placeholder.com/346x399" />
      </AuthorizationOnly>
      <EventContentStyled>
        <TitleStyled>
          <Title size="small" text="Название" />
        </TitleStyled>
        <AuthorizationOnly fallback={<AuthButton />}>
          <Button customtype="primary">
            <Link to={`/events/${id}`}>запустить</Link>
          </Button>
        </AuthorizationOnly>
      </EventContentStyled>
    </EventStyled>
  )
}
