import styled from 'styled-components'
import socials from './socials.png'

type Social = 'vk' | 'facebook' | 'twitter'

type SocialsType = {
  type: 'vk' | 'facebook' | 'twitter'
}

const BackgroundPosition: {
  [key in Social]: string
} = {
  vk: '-5px 1px',
  facebook: '-31px 1px',
  twitter: '-57px 1px',
}

export const SocialsStyled = styled.div`
  display: flex;
  margin-left: -2px;
`

// TODO: найти svg иконки
export const SocialStyled = styled.a<SocialsType>`
  width: 23px;
  height: 25px;
  margin-right: 3px;
  background-image: url(${socials});
  background-repeat: no-repeat;
  background-position: ${(props) => BackgroundPosition[props.type as Social]};
`
