import React from 'react'
import { useSelector } from 'rdx/index'
import { userSelector } from 'rdx/ducks/auth'
import { Row, Col, ImageStyled, HeaderStyled } from './styles'

export const User: React.FC<{}> = (props) => {
  const user = useSelector(state => userSelector(state))

  return (
    <div>
        <HeaderStyled>
          <ImageStyled>
              <img src={user?.avatar} />
          </ImageStyled>
          <div>{user?.first_name} {user?.last_name} {user?.secon_name}</div>
        </HeaderStyled>
  
        <div className="mb-15">
            <Row>
                <Col>Роль:</Col>
                <Col>Роль</Col>
            </Row>
            <Row>
                <Col>Email:</Col>
                <Col>{user?.email}</Col>
            </Row>
            <Row>
                <Col>Телефон:</Col>
                <Col>{user?.phone || 'телефон не указан'}</Col>
            </Row>
        </div>
      </div>
  )
}
