import React from 'react'
import { Link } from 'react-router-dom'
import { UlStyled, LIStyled, HeaderStyled } from './styles'
import usa from './usa.png'
import { useSelector } from 'rdx/index'
import { AppTextSelector } from 'rdx/ducks/internationalization'
import { logout } from 'rdx/ducks/auth'
import { useDispatch } from 'react-redux'
import { internationalizationActions } from 'rdx/ducks/internationalization'

export function AppHeader() {
  const dispatch = useDispatch()
  const TextMap = useSelector((state) => AppTextSelector(state))
  console.log('TextMap', TextMap)

  return (
    <HeaderStyled>
      <div className="container">
        <UlStyled>
          <LIStyled>
            <Link to="/">{TextMap.header.mainPage}</Link>
          </LIStyled>
          <LIStyled>
            <Link to="/auth">auth</Link>
          </LIStyled>
          <LIStyled>
            <Link to="/auth/login">login</Link>
          </LIStyled>
          <LIStyled>
            <Link to="/auth/registration">registration</Link>
          </LIStyled>
          <LIStyled>
            <Link to="/events">events</Link>
          </LIStyled>

          <LIStyled>
            <Link to="/lk">lk</Link>
          </LIStyled>

          <LIStyled>
            <Link to="/admin">admin</Link>
          </LIStyled>

          <LIStyled onClick={() => dispatch(logout())}>
            <span>logout</span>
          </LIStyled>

          <LIStyled
            onClick={() =>
              dispatch(internationalizationActions.setLanguage('usa'))
            }
          >
            <img src={usa} />
          </LIStyled>
        </UlStyled>
      </div>
    </HeaderStyled>
  )
}
