import React from 'react'
export * from './Input'
import { WrapperStyled, InputStyled, PlaceholderStyled } from './styles'
import { InputProps } from 'formik-antd'
import { Title } from 'src/components'

export const Input: React.FC<InputProps> = (props) => {
  const { placeholder, value } = props

  return (
    <WrapperStyled>
      <InputStyled {...props} />
      {placeholder && (
        <PlaceholderStyled value={value}>
          <Title size="smallest" text={placeholder} />
        </PlaceholderStyled>
      )}
    </WrapperStyled>
  )
}
