import styled from 'styled-components'
import { colors } from 'styles/colors'
import { TextProps, AlignType, SizeType } from './types'

// TODO : разобраться почему без assertion ошибка с "symbol",
type PropsType<T> = {
  [key in T as SizeType | AlignType]?: string
};

const fontSize: PropsType<SizeType> = {
  large: '20',
  medium: '16',
  small: '15',
}

const lineHeight: PropsType<SizeType> = {
  large: '23',
  medium: '28',
  small: '22',
}

const textAlign: PropsType<AlignType> = {
  center: 'center',
  right: 'right',
}

export const TextStyled = styled.p<TextProps>`
  font-family: 'Roboto';
  font-size: ${props => fontSize[props.size as SizeType]}px;
  line-height: ${props => lineHeight[props.size as SizeType]}px;
  font-style: normal;
  font-weight: 400;
  letter-spacing: ${props => props.size === 'medium' ? '0.02em': '0em'};
  text-align: ${props => textAlign[props.align as AlignType] || 'left'};
  color: ${colors.SEMILIGHT};
`
