import React from 'react'
import { useSelector } from 'rdx/index'
import { authorizedSelector } from 'rdx/ducks/auth'

type AuthorizationOnlyProps = {
  // TODO : типизировать
  fallback: any
  children: any
}

export const AuthorizationOnly: React.FC<AuthorizationOnlyProps> = (props) => {
  const { fallback } = props
  const isAuthorized = useSelector(state => authorizedSelector(state))

  if (isAuthorized) {
    return <>{props.children}</>
  }

  if (fallback) {
    if (typeof fallback === 'function') {
      return fallback()
    }

    return fallback
  }

  return null
}
