import React, { useEffect } from 'react'
import { ProfileControls } from '../ProfileControls'
import * as Yup from 'yup'
import { useDispatch } from 'react-redux'
import { Formik } from 'formik'
import { Form } from 'formik-antd'
import { EyeOutlined } from '@ant-design/icons'
import { Input, ErrorsComponent } from 'src/components'
import { passwordUpdate } from 'rdx/ducks/auth'
import { PasswordUpdateFormData } from 'types/user'
import { useSelector } from 'rdx/index'
import { formErrorsActions, formErrorSelector } from 'rdx/ducks/formErrors'
import { Forms } from 'types/form'
import { ErrorStyled } from './styles'

export const PasswordSettings: React.FC<{}> = (props) => {
  const dispatch = useDispatch()
  const networkError = useSelector((state) =>
    formErrorSelector(state, Forms.passwordSettings),
  )

  useEffect(() => {
    dispatch(formErrorsActions.resetFormError(Forms.passwordSettings))
  }, [])

  const PasswordSettingsSchema = Yup.object().shape({
    old_password: Yup.string().required('Current password is required'),
    new_password: Yup.string().required('New password is required'),
    new_password_repeated: Yup.string()
      .required('New password is Required')
      .oneOf([Yup.ref('new_password'), null], 'Passwords must match'),
  })

  const initialValues: PasswordUpdateFormData = {
    old_password: '',
    new_password: '',
    new_password_repeated: '',
  }

  return (
    <div>
      <Formik
        initialValues={initialValues}
        validationSchema={PasswordSettingsSchema}
        onSubmit={({ old_password, new_password }) => {
          dispatch(
            passwordUpdate({
              old_password,
              new_password,
            }),
          )
        }}
        validateOnChange={false}
        validateOnBlur={false}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          submitForm,
        }) => (
          <Form>
            <ProfileControls
              title="Личные данные"
              subtitle="Изменить пароль учетной записи"
              onSubmit={submitForm}
            />
            <div>
              <div>
                <div className="mb-15">
                  <Input
                    type="password"
                    name="old_password"
                    placeholder="Текущий пароль"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.old_password}
                    prefix={<EyeOutlined />}
                  />
                </div>
                <div className="mb-15">
                  <Input
                    type="password"
                    name="new_password"
                    placeholder="Новый пароль"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.new_password}
                    prefix={<EyeOutlined />}
                  />
                </div>
                <div className="mb-15">
                  <Input
                    type="password"
                    name="new_password_repeated"
                    placeholder="Подтвердите пароль"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.new_password_repeated}
                    prefix={<EyeOutlined />}
                  />
                </div>
              </div>
            </div>

            <ErrorsComponent
              errors={errors}
              touched={touched}
              networkError={networkError}
              StyledComponent={ErrorStyled}
            />
          </Form>
        )}
      </Formik>
    </div>
  )
}
