import React, { useRef } from 'react'
import { WrapperStyled, IframeStyled } from './styles'
import { useLocation } from 'react-router-dom'

const URLS_BY_ID = {
  '1': 'https://s-dt2.cloud.gcore.lu/viexpo-webgl/aq-tutorial/tutorial/index.html',
  '2': 'https://cdn.viexpo.ru/viexpo-webgl/react_unity_test/index.html',
  '3': 'https://cdn.viexpo.ru/viexpo-webgl/webgl_aq_client/index.html'
}

export const EventPage: React.FC<{}> = (props) => {
  const location = useLocation();

  const urlParts = location.pathname.split('/');
  const id = urlParts[urlParts.length - 1]
  
  const url = URLS_BY_ID[id]

  const webglRef = useRef();

  // let setToken = () => {
  //   webglRef.current.contentWindow.postMessage(JSON.stringify({
  //     key: 'token',
  //     data: localStorage.token
  //   }), "*");
  // }

  return (
    <div className="page-content">
      <div className="container">
        <WrapperStyled>
          <IframeStyled
            ref={webglRef}
            src={url}
            title="Aquarius Build"
            allowFullScreen
            // onLoad={setToken}
          >
          </IframeStyled>
        </WrapperStyled>
      </div>
    </div>
  )
}
