import React from 'react'
import { Auth } from 'src/components'
import { RouteType } from 'types/router'
import { AuthorizationOnly } from 'src/components'
import { Redirect } from 'react-router-dom'

type AuthScreenProps = {
  routes: RouteType[]
}

const AuthScreenComponent = (props: any) => (
  <div className="page-content">
    <Auth {...props} />
  </div>
)

export const AuthScreen: React.FC<AuthScreenProps> = (props) => {
  return (
    <AuthorizationOnly fallback={<AuthScreenComponent {...props} />}>
      <Redirect to="/" />
    </AuthorizationOnly>
  )
}
