import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { FormErrorsInitialState, FormError } from './'
import { Forms } from 'types/form'

const initialState: FormErrorsInitialState = {
  errors: {},
}

const formErrorsSlice = createSlice({
  name: 'formErrors',
  initialState,
  reducers: {
    setError: (
      state,
      { payload: { form, error } }: PayloadAction<FormError>,
    ) => ({
      errors: {
        ...state.errors,
        [form]: error,
      },
    }),
    resetFormError: (state, { payload }: PayloadAction<Forms>) => {
      const newState = {
        ...state,
        errors: {
          ...state.errors,
        },
      }

      delete newState.errors[payload]

      return newState
    },
  },
  extraReducers: {},
})

export const formErrorsReducer = formErrorsSlice.reducer
export const formErrorsActions = formErrorsSlice.actions
