import React from 'react'
import logo from './logo.png'
import { LogoStyled, LogoTextStyled } from './styles'
import { LogoProps } from './types'

export const Logo: React.FC<LogoProps> = (props) => {
  const { showText } = props

  return (
    <LogoStyled {...props}>
      <img src={logo} />
      {showText && <LogoTextStyled {...props}>ViExpo</LogoTextStyled>}
    </LogoStyled>
  )
}
