import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { AuthInitialState } from './'
import {
  login,
  logout,
  checkSession,
  profileUpdate,
  passwordUpdate,
  avatarUpdate,
  emailUpdate,
} from './thunks'
import {
  User,
  UserUpdateFormData,
  PasswordUpdateData,
} from 'types/user'

const initialState: AuthInitialState = {
  user: null,
  isAuthorized: false,
  isFetching: false,
  isFetched: false,
  isCheckSessionFetching: false,
  isCheckSessionFetched: false,
}

const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {},
  extraReducers: {
    [login.pending.toString()]: (state, action) => {
      state.isFetching = true
      state.isFetched = false
      state.isAuthorized = false
      state.user = null
      return state
    },
    [login.fulfilled.toString()]: (state, { payload }: PayloadAction<User>) => {
      state.isFetching = false
      state.isFetched = true
      state.isAuthorized = true
      state.user = payload
      return state
    },
    [login.rejected.toString()]: (state, action) => {
      state.isFetching = false
      state.isFetched = false
      return state
    },

    [logout.pending.toString()]: (state, action) => {
      state.isFetching = true
      state.isFetched = false
      return state
    },
    [logout.fulfilled.toString()]: (
      state,
      { payload }: PayloadAction<User>,
    ) => {
      state.isFetching = false
      state.isFetched = true
      state.isAuthorized = false
      state.user = null
      return state
    },
    [logout.rejected.toString()]: (state, action) => {
      state.isFetching = false
      state.isFetched = false
      return state
    },

    [checkSession.pending.toString()]: (state, action) => {
      state.isCheckSessionFetching = true
      state.isCheckSessionFetched = false
      return state
    },
    [checkSession.fulfilled.toString()]: (
      state,
      { payload }: PayloadAction<User>,
    ) => {
      state.isCheckSessionFetching = false
      state.isCheckSessionFetched = true
      state.isAuthorized = true
      state.user = payload
      return state
    },
    [checkSession.rejected.toString()]: (state, action) => {
      state.isCheckSessionFetching = false
      state.isCheckSessionFetched = false
      return state
    },

    [profileUpdate.pending.toString()]: (state, action) => {
      state.isFetching = true
      state.isFetched = false
      return state
    },
    [profileUpdate.fulfilled.toString()]: (
      state,
      { payload }: PayloadAction<UserUpdateFormData>,
    ) => {
      state.isFetching = false
      state.isFetched = true
      state.user = {
        ...(state.user as User),
        ...payload,
      }
      return state
    },
    [profileUpdate.rejected.toString()]: (state, action) => {
      state.isFetching = false
      state.isFetched = false
      return state
    },

    [passwordUpdate.pending.toString()]: (state, action) => {
      state.isFetching = true
      state.isFetched = false
      return state
    },
    [passwordUpdate.fulfilled.toString()]: (
      state,
      { payload }: PayloadAction<PasswordUpdateData>,
    ) => {
      state.isFetching = false
      state.isFetched = true
      state.user = null
      state.isAuthorized = false
      return state
    },
    [passwordUpdate.rejected.toString()]: (state, action) => {
      state.isFetching = false
      state.isFetched = false
      return state
    },

    [avatarUpdate.pending.toString()]: (state, action) => {
      state.isFetching = true
      state.isFetched = false
      return state
    },
    [avatarUpdate.fulfilled.toString()]: (
      state,
      { payload }: PayloadAction<string>,
    ) => {
      state.isFetching = false
      state.isFetched = true
      state.user = {
        ...(state.user as User),
        avatar: payload,
      }
      return state
    },
    [avatarUpdate.rejected.toString()]: (state, action) => {
      state.isFetching = false
      state.isFetched = false
      return state
    },

    [emailUpdate.pending.toString()]: (state, action) => {
      state.isFetching = true
      state.isFetched = false
      return state
    },
    [emailUpdate.fulfilled.toString()]: (
      state,
      { payload }: PayloadAction<string>,
    ) => {
      state.isFetching = false
      state.isFetched = true
      state.user = null
      state.isAuthorized = false
      return state
    },
    [emailUpdate.rejected.toString()]: (state, action) => {
      state.isFetching = false
      state.isFetched = false
      return state
    },
  },
})

export const authReducer = authSlice.reducer
export const authActions = authSlice.actions
