import React, { useRef, useEffect, useState, useCallback } from 'react'
import { ProfileControls } from '../'
import { Avatar } from 'src/components'
import { useDispatch } from 'react-redux'
import { avatarUpdate, userSelector, avatarDelete } from 'rdx/ducks/auth'
import { Forms } from 'types/form'
import { formErrorsActions, formErrorSelector } from 'rdx/ducks/formErrors'
import { useSelector } from 'rdx/index'
import { Modal } from 'antd'
import { EditOutlined } from '@ant-design/icons'
import { ImageWrapperStyled, ImageStyled, EditButtonStyled } from './styles'
import { Input } from 'antd';
import { toBase64 } from 'src/utils'

export const AvatarSettings: React.FC<{}> = (props) => {
  const dispatch = useDispatch()
  const [image, setImage] = useState(null)
  const user = useSelector(state => userSelector(state))
  const cropperRef = useRef<HTMLImageElement>(null)
  const networkError = useSelector((state) =>
    formErrorSelector(state, Forms.avatarUpdate),
  )

  const [isModalVisible, setIsModalVisible] = useState(false)

  const showModal = () => {
    setIsModalVisible(true)
  }

  const handleOk = () => {
    setIsModalVisible(false)
    onCrop()
  }

  const handleCancel = () => {
    setIsModalVisible(false)
  }

  useEffect(() => {
    dispatch(formErrorsActions.resetFormError(Forms.avatarUpdate))
  }, [])

  const onCrop = () => {
    const imageElement: any = cropperRef?.current
    const cropper: any = imageElement?.cropper

    dispatch(avatarUpdate(cropper.getCroppedCanvas().toDataURL()))
  }

  const handleAvatarDelete = useCallback(() => {
    dispatch(avatarDelete())
  }, [])

  return (
    <div>
      <ProfileControls
        title="Аватар профиля"
        subtitle="Изменить фото учетной записи"
        onSubmit={handleAvatarDelete}
        submitButtonText={"Удалить"}
      />

      <ImageWrapperStyled>
        <ImageStyled src={user!.avatar} />
        <EditButtonStyled for="avatar">
          <EditOutlined />
          <Input id="avatar" type="file" hidden onChange={async item => {
            // TODO : ts
            const image64 = await toBase64(item.target.files[0])
            setImage(image64)
            showModal()
          }} />
        </EditButtonStyled>
      </ImageWrapperStyled>

      <Modal
        title="Выберите размер изображения"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        {image && (
          <Avatar ref={cropperRef} src={image} />
        )}
      </Modal>

      {networkError && <p>networkError</p>}
    </div>
  )
}
