import React, { useEffect } from 'react'
import { Formik } from 'formik'
import { Form } from 'formik-antd'
import {
  Title,
  Text,
  Logo,
  Button,
  Input,
  ErrorsComponent,
} from 'src/components'
import { AuthFormData } from './types'
import { useDispatch } from 'react-redux'
import { login } from 'rdx/ducks/auth'
import { Link } from 'react-router-dom'
import { MailOutlined, EyeOutlined } from '@ant-design/icons'
import {
  LoginStyled,
  LogoStyled,
  InputWrapper,
  ButtonEnter,
  ButtonRegister,
  LoginTitle,
  TextStyled,
  ErrorStyled,
} from './styles'
import { useSelector } from 'rdx/index'
import { formErrorsActions, formErrorSelector } from 'rdx/ducks/formErrors'
import { Forms } from 'types/form'
import * as Yup from 'yup'

export const Login: React.FC<{}> = () => {
  const dispatch = useDispatch()
  const networkError = useSelector((state) =>
    formErrorSelector(state, Forms.login),
  )

  useEffect(() => {
    dispatch(formErrorsActions.resetFormError(Forms.login))
  }, [])

  const initialValues: AuthFormData = {
    email: '',
    password: '',
  }

  const LoginSettingsSchema = Yup.object().shape({
    email: Yup.string()
      .required('Email is Required')
      .email('Invalid email address'),
    password: Yup.string()
      .required('Password is Required')
      .min(6, 'Password is at least 6 characters'),
  })

  return (
    <LoginStyled>
      <LogoStyled>
        <Logo showText={true} size="small" />
      </LogoStyled>

      <LoginTitle>
        <Title size="medium" align="center" text="Авторизация" />
      </LoginTitle>

      <Formik
        initialValues={initialValues}
        onSubmit={(values) => {
          dispatch(login(values))
        }}
        validateOnChange={false}
        validateOnBlur={false}
        validationSchema={LoginSettingsSchema}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          submitForm,
          /* and other goodies */
        }) => (
          <Form>
            <InputWrapper>
              <Input
                type="email"
                name="email"
                placeholder="E-mail"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.email}
                prefix={<MailOutlined />}
              />
            </InputWrapper>

            <InputWrapper>
              <Input
                type="password"
                name="password"
                placeholder="Пароль"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.password}
                prefix={<EyeOutlined />}
              />
            </InputWrapper>

            <ErrorsComponent
              errors={errors}
              touched={touched}
              networkError={networkError}
              StyledComponent={ErrorStyled}
            />

            <ButtonEnter>
              <Button
                customtype="primary"
                onClick={() => submitForm()}
              >
                Войти
              </Button>
            </ButtonEnter>
          </Form>
        )}
      </Formik>

      <TextStyled>
        <Text size="medium" align="center" text="Еще нет аккаунта?" />
      </TextStyled>

      <ButtonRegister>
        <Link to="/auth/registration">
          <Button customtype="default" disabled={false}>
            Зарегистрироваться
          </Button>
        </Link>
      </ButtonRegister>
    </LoginStyled>
  )
}
