import styled from 'styled-components'
import { colors } from 'styles/colors'
import { LogoProps, SizeType } from './types'

// TODO : разобраться почему без assertion ошибка с "symbol",
type PropsType<T> = {
    [key in T as SizeType]?: string
  };
  
const width: PropsType<SizeType> = {
    large: '70',
    medium: '85',
    small: '40',
}

const fontSize: PropsType<SizeType> = {
    large: '40',
    medium: '',
    small: '15',
}

const lineHeight: PropsType<SizeType> = {
    large: '47',
    medium: '',
    small: '18',
}

const marginLeft: PropsType<SizeType> = {
    large: '12',
    medium: '',
    small: '7',
}

export const LogoStyled = styled.div<LogoProps>`
    display: flex;
    align-items: center;
    
    img {
        width: ${props => width[props.size as SizeType]}px;
    }
`

export const LogoTextStyled = styled.span<LogoProps>`
    padding-top: 7px;
    font-size: ${props => fontSize[props.size as SizeType]}px;
    line-height: ${props => lineHeight[props.size as SizeType]}px;
    color: ${colors.WHITE};
    margin-left: ${props => marginLeft[props.size as SizeType]}px;
`
