import React, { useEffect, useState } from 'react'
import { Provider as ReduxProvider, useDispatch } from 'react-redux'
import { store, useSelector } from 'rdx/index'
import { isLoaderVisibleSelector } from 'rdx/ducks/app'
import { AppRouter } from 'src/AppRouter'
import { AppStyled } from './styles'
import { checkSession, authIsCheckSessionFetchingSelector } from 'rdx/ducks/auth'
import { Loader } from 'src/components'

const AppComponent = () => {
  const dispatch = useDispatch()
  const [isLoaderShow, setIsLoaderShow] = useState(true)
  const isCheckSessionFetching = useSelector(state => authIsCheckSessionFetchingSelector(state))
  const isLoaderVisible = useSelector(state => isLoaderVisibleSelector(state))

  useEffect(() => {
    dispatch(checkSession())
  }, [dispatch])

  if (isCheckSessionFetching || isLoaderShow) {
    if (isCheckSessionFetching && isLoaderShow) {
      setIsLoaderShow(false);
    }
    
    return <Loader />
  }

  return (
    <div>
      <AppRouter />
      {isLoaderVisible && (
        <Loader />
      )}
    </div>
  )
}

export function App() {
  return (
    <AppStyled>
      <ReduxProvider store={store}>
        <AppComponent />
      </ReduxProvider>
    </AppStyled>
  )
}
