import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { InternationalizationInitialState } from './'
import { Language } from 'types/language'

const initialState: InternationalizationInitialState = {
  language: 'russia',
}

const internationalizationSlice = createSlice({
  name: 'internationalization',
  initialState,
  reducers: {
    setLanguage: (state, action: PayloadAction<Language>) => ({
      ...state,
      language: action.payload,
    }),
  },
  extraReducers: {},
})

export const internationalizationReducer = internationalizationSlice.reducer
export const internationalizationActions = internationalizationSlice.actions
