import React, { useEffect } from 'react'
import { Formik } from 'formik'
import { RegistrationFormData } from './types'
import { Title, Text, Button, Input, ErrorsComponent } from 'src/components'
import { useDispatch } from 'react-redux'
import { register } from 'rdx/ducks/auth'
import { Form } from 'formik-antd'
import { UserOutlined, MailOutlined, EyeOutlined } from '@ant-design/icons'
import {
  RegistrationStyled,
  InputWrapper,
  ButtonWrapper,
  RegistrationTitle,
  TextStyled,
  ErrorStyled,
} from './styles'
import * as Yup from 'yup'
import { Forms } from 'types/form'
import { formErrorsActions, formErrorSelector } from 'rdx/ducks/formErrors'
import { useSelector } from 'rdx/index'

export const Registration: React.FC<{}> = () => {
  const dispatch = useDispatch()
  const networkError = useSelector((state) =>
    formErrorSelector(state, Forms.register),
  )

  useEffect(() => {
    dispatch(formErrorsActions.resetFormError(Forms.register))
  }, [])

  const PasswordSettingsSchema = Yup.object().shape({
    first_name: Yup.string().required('First name is required'),
    email: Yup.string()
      .required('Email is Required')
      .email('Invalid email address'),
    password: Yup.string()
      .required('Password is Required')
      .min(6, 'Password is at least 6 characters'),
  })

  const initialValues: RegistrationFormData = {
    first_name: '',
    email: '',
    password: '',
  }

  return (
    <RegistrationStyled>
      <RegistrationTitle>
        <Title size="medium" align="center" text="Регистрация" />
      </RegistrationTitle>
      <Formik
        initialValues={initialValues}
        onSubmit={(values: RegistrationFormData) => {
          dispatch(register(values))
        }}
        validateOnChange={false}
        validateOnBlur={false}
        validationSchema={PasswordSettingsSchema}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          submitForm,
        }) => (
          <Form>
            <InputWrapper>
              <Input
                type="text"
                name="first_name"
                placeholder="Как вас зовут?"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.first_name}
                prefix={<UserOutlined />}
              />
            </InputWrapper>

            <InputWrapper>
              <Input
                type="email"
                name="email"
                placeholder="Введите ваш Email"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.email}
                prefix={<MailOutlined />}
              />
            </InputWrapper>

            <InputWrapper>
              <Input
                type="password"
                name="password"
                placeholder="Придумайте пароль"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.password}
                prefix={<EyeOutlined />}
              />
            </InputWrapper>

            <TextStyled>
              <Text
                size="small"
                text="Вы всегда сможете отредактировать данные в личном кабинете"
                align="center"
              />
            </TextStyled>

            <ErrorsComponent
              errors={errors}
              touched={touched}
              networkError={networkError}
              StyledComponent={ErrorStyled}
            />

            <ButtonWrapper>
              <Button
                customtype="primary"
                disabled={false}
                onClick={submitForm}
              >
                Зарегистрироваться
              </Button>
            </ButtonWrapper>
          </Form>
        )}
      </Formik>
    </RegistrationStyled>
  )
}
