import React from 'react'

// TODO : ts
type ErrorsProps = {
  errors: any
  touched: any
  networkError?: string
  StyledComponent: any
}

export const ErrorsComponent: React.FC<ErrorsProps> = ({
  errors,
  touched,
  networkError,
  StyledComponent,
}) => {
  const keys = Object.keys(errors)

  if (!keys.length && !networkError) {
    return null
  }

  for (let key of keys) {
    if (errors[key] && touched[key]) {
      return <StyledComponent>{errors[key]}</StyledComponent>
    }
  }

  return <StyledComponent>{networkError}</StyledComponent>
}
