import React from 'react'
import { Router, Switch, Redirect } from 'react-router-dom'
import {
  AppScreen,
  AuthScreen,
  EventsScreen,
  ProfileScreen,
  AdminScreen,
} from 'src/screens'
import { Login } from 'src/components/Auth/components/Login'
import { Registration } from 'src/components/Auth/components/Registration'
import { AppHeader } from 'src/components'
import { RouteType } from 'types/router'
import { RouteWithSubRoutes } from 'src/components'
import {
  ProfileSettings,
  PasswordSettings,
  AvatarSettings,
  EmailSettings,
} from 'src/components/Profile/components'
import { EventPage, EventList } from 'src/components/Events/components'
import { Events as EventsTableComponent } from 'src/components/Admin/components'
import history from 'src/history'

const RedirectToLogin = () => {
  return <Redirect to="/auth/login" />
}

const RedirectToEvents = () => {
  return <Redirect to="/admin/events" />
}

const RedirectToProfileSettings = () => {
  return <Redirect to="/lk/profile-settings" />
}

const routes: RouteType[] = [
  {
    path: '/auth',
    component: AuthScreen,
    routes: [
      {
        path: '/auth',
        component: RedirectToLogin,
        exact: true,
      },
      {
        path: '/auth/login',
        component: Login,
      },
      {
        path: '/auth/registration',
        component: Registration,
      },
    ],
  },
  {
    path: '/',
    component: AppScreen,
    exact: true,
  },
  {
    path: '/events',
    component: EventsScreen,
    routes: [
      {
        path: '/events',
        exact: true,
        component: EventList,
      },
      {
        path: '/events/:id',
        component: EventPage,
      },
    ]
  },
  {
    path: '/lk',
    component: ProfileScreen,
    routes: [
      {
        path: '/lk',
        component: RedirectToProfileSettings,
        exact: true,
      },
      {
        path: '/lk/profile-settings',
        component: ProfileSettings,
      },
      {
        path: '/lk/avatar-settings',
        component: AvatarSettings,
      },
      {
        path: '/lk/password-settings',
        component: PasswordSettings,
      },
      {
        path: '/lk/email-settings',
        component: EmailSettings,
      },
    ],
  },
  {
    path: '/admin',
    component: AdminScreen,
    routes: [
      {
        path: '/admin',
        component: RedirectToEvents,
        exact: true,
      },
      {
        path: '/admin/events',
        component: EventsTableComponent,
      },
      {
        path: '/admin/customers',
        component: EventsTableComponent,
      },
      {
        path: '/admin/reports',
        component: EventsTableComponent,
      },
      {
        path: '/admin/settings',
        component: EventsTableComponent,
      },
    ],
  },
]

export const AppRouter = () => {
  return (
    <Router history={history}>
      <AppHeader />
      <Switch>
        {routes.map((route, i) => (
          <RouteWithSubRoutes key={i} {...route} />
        ))}
      </Switch>
    </Router>
  )
}
