import React, { useEffect } from 'react'
import { ProfileControls } from '../'
import { Input, ErrorsComponent } from 'src/components'
import { Formik } from 'formik'
import { Form } from 'formik-antd'
import {
  UserOutlined,
  MailOutlined,
  BankOutlined,
  PhoneOutlined,
  IdcardOutlined,
} from '@ant-design/icons'
import * as Yup from 'yup'
import { userSelector } from 'rdx/ducks/auth'
import { useSelector } from 'rdx/index'
import { useDispatch } from 'react-redux'
import { profileUpdate } from 'rdx/ducks/auth'
import { UserUpdateFormData } from 'types/user'
import { Forms } from 'types/form'
import { formErrorsActions, formErrorSelector } from 'rdx/ducks/formErrors'
import { ErrorStyled } from './styles'

const ProfileSettingsSchema = Yup.object().shape({
  first_name: Yup.string().required('First name is required'),
  last_name: Yup.string(),
  company: Yup.string(),
  position: Yup.string(),
  phone: Yup.string(),
})

export const ProfileSettings: React.FC<{}> = (props) => {
  const dispatch = useDispatch()
  const userData = useSelector((state) => userSelector(state))
  const networkError = useSelector((state) =>
    formErrorSelector(state, Forms.register),
  )

  useEffect(() => {
    dispatch(formErrorsActions.resetFormError(Forms.profileSettings))
  }, [])

  const initialValues: UserUpdateFormData = {
    first_name: userData?.first_name || '',
    last_name: userData?.last_name || '',
    secon_name: userData?.secon_name || '',
    company: userData?.company || '',
    position: userData?.position || '',
    phone: userData?.phone || '',
  }

  return (
    <div>
      <Formik
        initialValues={initialValues}
        validationSchema={ProfileSettingsSchema}
        onSubmit={(values) => {
          dispatch(profileUpdate(values))
        }}
        validateOnChange={false}
        validateOnBlur={false}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          submitForm,
        }) => (
          <Form>
            <ProfileControls
              title="Личные данные"
              subtitle="Обновите ваши личные данные"
              onSubmit={submitForm}
            />
            <div>
              <div className="mb-15">
                <Input
                  type="text"
                  name="first_name"
                  placeholder="Имя"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.first_name}
                  prefix={<UserOutlined />}
                />
              </div>
              <div className="mb-15">
                <Input
                  type="text"
                  name="last_name"
                  placeholder="Фамилия"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.last_name}
                  prefix={<UserOutlined />}
                />
              </div>
              <div className="mb-15">
                <Input
                  type="text"
                  name="secon_name"
                  placeholder="Отчество"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.secon_name}
                  prefix={<UserOutlined />}
                />
              </div>
              <div className="mb-15">
                <Input
                  type="text"
                  name="company"
                  placeholder="Компания"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.company}
                  prefix={<BankOutlined />}
                />
              </div>
              <div className="mb-15">
                <Input
                  type="text"
                  name="position"
                  placeholder="Должность"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.position}
                  prefix={<IdcardOutlined />}
                />
              </div>
              <div className="mb-15">
                <Input
                  type="text"
                  name="phone"
                  placeholder="Телефон"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.phone}
                  prefix={<PhoneOutlined />}
                />
              </div>
            </div>

            <ErrorsComponent
              errors={errors}
              touched={touched}
              networkError={networkError}
              StyledComponent={ErrorStyled}
            />
          </Form>
        )}
      </Formik>
    </div>
  )
}
