import { ax } from './setup'
import { UserUpdateFormData, PasswordUpdateData } from 'types/user'

export const PASSWORD_UPDATE = 'api/account/profile/password-update'
export const EMAIL_UPDATE = 'api/account/profile/email-update'
export const PROFILE_UPDATE = 'api/account/profile/profile-update'
export const OPEN_SESSIONS_LIST = 'api/account/profile/session-list'
export const CLOSE_SESSION = 'api/account/profile/session-close-target'
export const CLOSE_OTHER_SESSIONS = 'api/account/profile/session-close-other'
export const AVATAR_UPDATE = 'api/account/profile/avatar-update'

export const userAPI = {
  passwordUpdate(userData: PasswordUpdateData) {
    return ax.post(
      PASSWORD_UPDATE,
      { ...userData },
      {
        headers: {
          'x-viexpo-session-token': localStorage.getItem('current_session'),
        },
      },
    )
  },
  emailUpdate(email: string) {
    return ax.post(
      EMAIL_UPDATE,
      { email },
      {
        headers: {
          'x-viexpo-session-token': localStorage.getItem('current_session'),
        },
      },
    )
  },
  profileUpdate(userData: UserUpdateFormData) {
    return ax.post(
      PROFILE_UPDATE,
      { ...userData },
      {
        headers: {
          'x-viexpo-session-token': localStorage.getItem('current_session'),
        },
      },
    )
  },
  // TODO : ts
  avatarUpdate(formData) {
    return ax.post(AVATAR_UPDATE, formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
        'x-viexpo-session-token': localStorage.getItem('current_session'),
      },
    })
  },
}
