import React from 'react'
import { Switch } from 'react-router-dom'
import { RouteWithSubRoutes } from 'src/components'
import { RouteType } from 'types/router'
import { Welcome } from './components'
import { AuthStyled, FormWrapperStyled } from './styles'

type AuthProps = {
  routes: RouteType[]
}

export const Auth: React.FC<AuthProps> = ({ routes }) => {
  return (
    <div className="container">
      <AuthStyled>
        <div className="row">
          <div className="col">
            <Welcome />
          </div>
          <div className="col">
            <FormWrapperStyled>
              <Switch>
                {routes.map((route, i) => (
                  <RouteWithSubRoutes key={i} {...route} />
                ))}
              </Switch>
            </FormWrapperStyled>
          </div>
        </div>
      </AuthStyled>
    </div>
  )
}
