import AntButton from 'antd/lib/button/index'
import styled from 'styled-components'
import { colors } from 'styles/colors'
import { ButtonProps, ButtonType } from './types'

type TypeProps = {
  [key in ButtonType]?: string
}

const background: TypeProps = {
  primary: colors.GRADIENT_PRIMARY,
  secondary: colors.GRADIENT_SECONDARY,
  default: 'transparent',
}

const backgroundHover: TypeProps = {
  primary: colors.GRADIENT_PRIMARY_HOVER,
  secondary: colors.GRADIENT_SECONDARY_HOVER,
  default: 'transparent',
}

const backgroundActive: TypeProps = {
  primary: colors.GRADIENT_PRIMARY_ACTIVE,
  secondary: colors.GRADIENT_SECONDARY_ACTIVE,
  default: 'transparent',
}

const boxShadow: TypeProps = {
  primary: '0px 10px 35px rgba(251, 0, 82, 0.4)',
  secondary: '0px 4px 30px rgba(13, 177, 217, 0.3)',
}

const boxShadowHover: TypeProps = {
  primary: '0px 10px 35px rgba(251, 0, 82, 0.4)',
  secondary: '0px 10px 35px rgba(13, 177, 217, 0.5)',
}

const boxShadowActive: TypeProps = {
  primary: '0px 10px 35px rgba(251, 0, 82, 0.4)',
  secondary: '0px 10px 35px rgba(13, 177, 217, 0.5)',
}

const border: TypeProps = {
  default: `1px solid ${colors.ADDITIONAL}`,
}

const borderHover: TypeProps = {
  default: `1px solid ${colors.SEMILIGHT}`,
}

const color: TypeProps = {
  default: colors.SEMILIGHT,
}

const colorActive: TypeProps = {
  default: colors.ADDITIONAL,
}

export const ButtonStyled = styled(AntButton)<ButtonProps>`
  min-width: 160px;
  height: 47px;
  padding: 12px 32px;
  background: ${(props) => background[props.customtype as ButtonType]};
  box-shadow: ${(props) => boxShadow[props.customtype as ButtonType]};
  border: ${(props) => border[props.customtype as ButtonType] || 'none'};
  border-radius: 40px;
  font-size: 20px;
  font-weight: 700;
  line-height: 23px;
  color: ${(props) => color[props.customtype as ButtonType] || colors.WHITE};

  &:hover {
    background: ${(props) => backgroundHover[props.customtype as ButtonType]};
    box-shadow: ${(props) => boxShadowHover[props.customtype as ButtonType]};
    color: ${(props) => color[props.customtype as ButtonType] || colors.WHITE};
    border: ${(props) => borderHover[props.customtype as ButtonType] || 'none'};
  }

  &:active {
    background: ${(props) => backgroundActive[props.customtype as ButtonType]};
    box-shadow: ${(props) => boxShadowActive[props.customtype as ButtonType]};
    color: ${(props) =>
      colorActive[props.customtype as ButtonType] || colors.WHITE};
  }

  &:focus {
    background: ${(props) => backgroundActive[props.customtype as ButtonType]};
    box-shadow: ${(props) => boxShadowActive[props.customtype as ButtonType]};
    color: ${(props) =>
      colorActive[props.customtype as ButtonType] || colors.WHITE};
  }
`
