import { ax } from './setup'
import { User, UserAuthFormData, UserRegisterFormData } from 'types/user'

export const LOGIN = 'api/account/enter/login'
export const REGISTER = 'api/account/enter/registration'
export const CURRENT_SESSION = 'api/account/enter/current-session'
export const LOGOUT = 'api/account/enter/logout'

export const authAPI = {
  getCurrentSession() {
    const uuid = localStorage.getItem('current_session')

    return ax.post(
      CURRENT_SESSION,
      {
        current_uuid: uuid,
      },
      {
        headers: {
          'x-viexpo-session-token': uuid,
        },
      },
    )
  },
  login(user: UserAuthFormData) {
    return ax.post(LOGIN, {
      email: user.email,
      password: user.password,
    })
  },
  logout() {
    return ax.post(
      LOGOUT,
      {},
      {
        headers: {
          'x-viexpo-session-token': localStorage.getItem('current_session'),
        },
      },
    )
  },
  register(user: UserRegisterFormData) {
    return ax.post(REGISTER, {
      email: user.email,
      password: user.password,
      first_name: user.first_name,
      // бек ожидает эти поля, хотя в макетах их нет, пока хардкодим здесь
      last_name: '',
      secon_name: '',
    })
  },
}
