import { createAsyncThunk } from '@reduxjs/toolkit'
import { authAPI } from 'src/api'
import { UserAuthFormData, UserRegisterFormData } from 'types/user'
import history from 'src/history'
import { formErrorsActions } from 'rdx/ducks/formErrors'
import { Forms } from 'types/form'
export * from './profileThunks'
import { appActions } from 'rdx/ducks/app'

// TODO : много повторяющегося кода, подумать как оптимизировать

export const login = createAsyncThunk(
  'auth/login',
  async (user: UserAuthFormData, { rejectWithValue, dispatch }) => {
    dispatch(appActions.showLoader())
    const data = await authAPI
      .login(user)
      .then((response) => {
        const { data } = response

        if (data.code === 0) {
          localStorage.setItem('current_session', data.response.session_uuid)
          history.push('/')
          dispatch(appActions.hideLoader())
          return data.response.user
        }

        throw Error()
      })
      .catch((error) => {
        console.log(error)
        dispatch(
          formErrorsActions.setError({
            form: Forms.login,
            error: error.message,
          }),
        )
        dispatch(appActions.hideLoader())
        return rejectWithValue(error)
      })
    return data
  },
)

