import React from 'react'
import Cropper from 'react-cropper'
import 'cropperjs/dist/cropper.css'
import { CropperStyled } from './styles'

// TODO : ts
type AvatarProps = {
  ref: any
  src: any
}

export const Avatar: React.FC<AvatarProps> = React.forwardRef((props, ref) => {
  const { src } = props

  return (
    <CropperStyled>
      <Cropper
        src={src}
        initialAspectRatio={1}
        aspectRatio={1}
        minCropBoxHeight={10}
        minCropBoxWidth={10}
        guides={false}
        zoomTo={0.5}
        ref={ref}
      />
    </CropperStyled>
  )
})
